body {
  height: 100%;
  /*overflow: hidden;*/
}

.App {
  position: relative;
  height: 100%;
  padding: 15% 0;
  /*max-height: 100vh;*/
}

#quote-box {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  position: relative;
  margin: 0 auto;
  width: 40%;
  border-radius: 5px;
  background-color: white;
  padding: 1em;
  min-height: 30vh;
  /*max-height: 40vh;*/
  text-align: center;

}

#author {
  display: block;
  position: relative;
  text-align: right;
  padding-right: 2em;
  opacity: 1;
  visibility: visible;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.hide {
  opacity: 0;
  visibility: hidden;
}

.buttons {
  display: flex;
  justify-content: space-around;
  margin-top: 1em;
  /*border: 1px solid red;*/
}

#new-quote {
  position: relative;
  text-align: center;
  border-radius: 5px;
  padding: 0.5em;
  color: white;
  user-select: none;
}

#new-quote:hover {
  cursor: pointer;
}

#tweet-quote {
  position: relative;
  border-radius: 5px;
  padding: 0.5em;
  text-decoration: none;
  user-select: none;
  color: white;
}

#tweet-quote:visited {
  color: white;
}